import React from 'react';
import { Row, Col } from 'react-bootstrap';

const WeatherOutput = props => {
  return (
    <div className="container output-container">
      {props.error && <h3>{props.error}</h3>}
      <h1>{props.city}</h1>
      <p>{props.country}</p>
      {props.temperature && <h2>{props.temperature}Â°C</h2>}
      <img src={props.icon} alt=""/>
      <p>{props.description}</p>
      <Row>
        <Col xs="6">
          {props.humidity >= 0 && <p>Humidity : <b>{props.humidity}%</b></p>}
        </Col>

        <Col xs="6">
          {props.windMph >= 0 && <p>Wind : <b>{props.windMph} MPH</b></p>}
        </Col>
      </Row>
    </div>
  )
}

export default WeatherOutput;
