import React from 'react';
import WeatherOutput from './WeatherOutput';
import { Tabs , Tab } from 'react-bootstrap';

const Main = props => {
  return (
    <main>
      <Tabs className="container" id="uncontrolled-tab-example">
        <Tab eventKey="weather" title="Weather">
            <WeatherOutput
            temperature = {props.output.temperature}
            country = {props.output.country}
            city = {props.output.city}
            humidity = {props.output.humidity}
            windMph = {props.output.windMph}
            description = {props.output.description}
            icon = {props.output.icon}
            error = {props.output.error}
            />
        </Tab>

        <Tab eventKey="images" title="Images">
          <div className="output-container">
            {props.output.error && <h3>{props.output.error}</h3>}
            {props.output.city && <p>{props.output.city} Images</p>}
          </div>
        </Tab>

        <Tab eventKey="wikipedia" title="Wikipedia">
          <div className="output-container">
            {props.output.error && <h3>{props.output.error}</h3>}
            {props.output.city && <p>{props.output.city} Wikipedia</p>}
          </div>
        </Tab>
      </Tabs>
    </main>
  )
}

export default Main;
