import React from 'react';
import { Button, Form, Col } from 'react-bootstrap';

const HeaderSearchBar = props => {
  return (
    <Form className="container" onSubmit={props.getWeather}>
      <Form.Row className="justify-content-center" controlId="formBasicEmail">
        <Col xs="7" sm="4" md="3" lg="2">
          <Form.Control type="text" name="city" placeholder="City..." />
        </Col>
        <Col sm="2">
          <Button variant="primary" type="submit">Search</Button>
        </Col>
      </Form.Row>
    </Form>
  )
}

export default HeaderSearchBar;
